:root {
  --white-dark: #f2f2f2;
  --grey-light: #d5dddf;
  --grey: #999;
  --grey-dark: #323436;
  --accent: #0086b3;
  --accent-dark: #086180;
  --accent-light: #249bc3;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: '-apple-system', BlinkMacSystemFont, Arial, sans-serif;
  font-size: 16px;
}
.container {
  max-width: 1164px;
  padding: 0 8px;
  margin: 0 auto;
}
@media (min-width: 600px) {
  .container {
    padding: 0 32px;
  }
}
section,
.no-print-break,
ul li,
ol li,
[data-run] {
  page-break-inside: avoid;
}
.can-print-break {
  page-break-inside: auto !important;
}
article {
  padding: 0 0 150px;
  page-break-after: always;
  border-bottom: 1px solid var(--grey-light);
  min-height: 100vh;
}
a {
  color: var(--accent);
}
a:hover {
  color: var(--accent-dark);
}
pre {
  border-radius: 4px;
  position: relative;
  white-space: pre-wrap;
  background-color: aliceblue !important;
  border: 1px solid darkgray;
}
pre:before {
  content: attr(data-lang);
  display: block;
  position: absolute;
  font-size: 12px;
  text-transform: uppercase;
  top: 1px;
  right: 2px;
}
code {
  background: aliceblue;
  margin: 0 8px;
  font-size: 14px;
  display: inline-block;
}
[data-run] {
  padding: 15px;
  border: none;
  width: 100%;
  min-height: 100px;
  max-height: 100vh;
  min-width: 300px;
  max-width: 100%;
  background: var(--white-dark);
  resize: both;
  overflow: auto;
}
h1 {
  height: 100vh;
  margin: 0 !important;
}
h1 .logo {
  font-size: 72px;
  position: absolute;
  left: 50%;
  top: 45vh;
  transform: translate(-50%, -50%);
}
h2 {
  font-size: 36px;
}
h3 {
  font-size: 24px;
}
h4 {
  font-size: 20px;
}
h5 {
  font-size: 18px;
}
h6 {
  font-size: 16px;
}
p {
  font-size: 16px;
  color: var(--grey-dark);
  margin: 2em 0 1em;
  font-weight: 400;
  word-break: break-word;
  line-height: 1.4;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--grey-dark);
  font-family: 'Philosopher', sans-serif;
  margin: 2em 0 1em;
  font-weight: 400;
}
ul,
ol {
  padding-left: 16px;
  color: var(--grey-dark);
  line-height: 1.4;
}
.image {
  text-align: center;
}
.image img {
  max-width: 100%;
}
.logo {
  display: inline-block;
  font-family: 'Philosopher', sans-serif !important;
  font-size: 30px;
  padding: 2px;
  line-height: 1;
  color: var(--grey-dark) !important;
  text-align: center !important;
  text-transform: uppercase;
  text-decoration: none;
}
.logo .top {
  display: block;
  font-weight: normal;
  border-bottom: 1px solid var(--grey-dark);
  padding: 0 5px;
}
.logo .bottom {
  display: block;
  border-bottom: 0.1em solid var(--grey-dark);
  padding: 1px 5px;
  font-size: 0.7em;
}
.aspect16-9 {
  width: 100%;
  height: 56vw;
  max-height: 620px;
  min-height: 180px;
}
.youtube a {
  display: block;
  position: relative;
}
.youtube a::after {
  content: '';
  display: block;
  background-image: url('data:image/png;base64,');
  background-repeat: no-repeat;
  background-position: center center;
  filter: grayscale(1) contrast(1.4) opacity(0.9);
  position: absolute;
  bottom: 50%;
  right: 50%;
  transform: translate(50%, 50%);
  width: 70px;
  height: 50px;
}
.youtube a:hover::after {
  filter: none;
}
.youtube a img {
  display: block;
  width: 100%;
}
.columns-2 {
  column-count: 2;
}
.button,
button {
  background-color: var(--accent);
  color: white;
  display: inline-block;
  text-decoration: none;
  border: none;
  padding: 0.5em 1em;
  border-radius: 4px;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  transition: background-color, box-shadow 150ms ease-in-out;
  cursor: pointer;
}
.button:hover,
button:hover {
  background: var(--accent-light);
  color: white;
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}
main {
  display: block;
  position: relative;
  grid-template-columns: 350px 1fr;
}
main > .main-menu {
  display: none;
  position: sticky;
  top: 0;
  left: 0;
  height: 100vh;
  max-width: 370px;
  padding: 1em 0;
  margin-top: 210vh;
  overflow-y: auto;
}
main > .main-menu #menu-switch-button {
  background-image: url(./assets/images/angle-left-sm.svg);
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: contain;
  margin-left: 1em;
  margin-bottom: 1em;
  width: 2em;
  height: 2em;
  opacity: 0.7;
}
main > .main-menu #menu-switch-button:not(:hover) {
  box-shadow: none;
}
main > .main-menu menu {
  margin: 0;
  list-style: none;
  padding: 0;
  min-width: 250px;
}
main > .main-menu menu li a {
  text-decoration: none;
  padding: 0.5em 1em;
  border-left: 0.3em solid transparent;
  display: inline-block;
  color: var(--grey);
}
main > .main-menu menu li a:hover {
  color: var(--accent);
}
main > .main-menu menu li a.active {
  border-left-color: var(--accent);
  color: var(--accent);
}
main.without-menu {
  grid-template-columns: 3em 1fr;
}
main.without-menu > .main-menu {
  width: 3em;
}
main.without-menu > .main-menu #menu-switch-button {
  background-image: url(./assets/images/angle-right-sm.svg);
}
main.without-menu > .main-menu menu {
  display: none;
}
@media (min-width: 1300px) {
  main {
    display: grid;
    padding-right: 3em;
  }
  main > .main-menu {
    display: block;
  }
  main > .main-content {
    max-width: calc(100vw - 6em);
  }
}
.print-only {
  display: none;
}
@media print {
  .print-only,
  main {
    display: block;
  }
  .no-print,
  .cp_embed_wrapper,
  main > .main-menu,
  main > .main-menu > #menu-switch-button {
    display: none;
  }
  article {
    min-height: auto;
    border-bottom: none;
    padding: 0;
  }
  a[target='_blank'] {
    display: inline-block;
  }
  a[target='_blank']::after {
    content: '[ ' attr(href) ' ]';
    font-size: 0.8em;
    margin: 0 1ex;
    display: inline-block;
  }
  a.button {
    background-color: white;
    color: var(--accent);
    box-shadow: none;
    border: none;
    padding: 0;
  }
  p {
    text-align: justify;
  }
  p a {
    text-align: left;
  }
}
